import React, { useEffect, useState } from "react";
import swal from "sweetalert";
import { useDispatch, useSelector } from "react-redux";
import { FetchLiveActives, addLiveActive } from "../../Network/ActiveApi";

const ManualPay = ({ softwareId }) => {
  //redux dispatch
  const dispatch = useDispatch();

  const [TransactionCode, setTransactionCode] = useState("");
  const [ConfirmCode, setConfirmCode] = useState("");
  const [Loading, setLoading] = useState(false);
  const Actives = useSelector((state) => state.active.actives);

  useEffect(() => {
    FetchLiveActives("", softwareId, 0, dispatch);
  }, []);

  const UploadCode = async () => {
    setLoading(true);
    await addLiveActive(
      { transaction: TransactionCode },
      softwareId,
      "",
      dispatch
    );
    setLoading(false);
    setTransactionCode("");
    setConfirmCode("");
  };

  const SaveCode = (e) => {
    e.preventDefault();
    if (TransactionCode == "" || TransactionCode != ConfirmCode) {
      return;
    }
    swal({
      title: `Warning!, To avoid spammers we only allow activation code request per institution once in a month.`,
      text: `Please confirm the payment transaction code and then continue ${TransactionCode}`,
      icon: "warning",
      buttons: true,
    })
      .then((willReturn) => {
        if (willReturn) {
          UploadCode();
        }
      })
      .catch((err) => console.log(err));
  };
  return (
    <div className="card my-2">
      {Actives.length > 0 ? (
        <table className="table table-sm">
          <thead>
            <tr>
              <th>Transaction Code</th>
              <th>Status</th>
              <th>Activation Code</th>
            </tr>
          </thead>
          <tbody>
            {Actives.map((active) => (
              <tr key={active.linker}>
                <td>{active.transaction}</td>

                <td>
                  {active.deleted != 0 ? (
                    <span className="text-danger">Deleted</span>
                  ) : active.status == 0 ? (
                    <span className="text-info">In Review</span>
                  ) : active.status == 2 ? (
                    <span className="text-danger">Rejected</span>
                  ) : active.status == 1 ? (
                    <span className="text-danger">Verified</span>
                  ) : (
                    ""
                  )}
                </td>
                <td>
                  {active.status == 0 ? "Check Later" : active.activation}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      ) : null}
      <div className="card-header">
        <p className="h6 text-primary text-center">
          Pay Through Paypal To{" "}
          <em className="btn-link">contact.techsystems@gmail.com</em> and enter
          the transaction code below.
        </p>
        <p className="text-center">
          In Case You need help email us at{" "}
          <em className="btn-link">contact.techsystems@gmail.com</em> or
          whatsapp <em className="btn-link">+254 703535116</em>
        </p>
      </div>
      <form onSubmit={SaveCode}>
        <div className="card-body">
          <div className="form-group">
            <label>Transaction Code:</label>
            <input
              className="rounded form-control"
              placeholder="Enter Transaction Code"
              value={TransactionCode}
              onChange={(e) => setTransactionCode(e.target.value)}
              required
            />
          </div>
          <hr />
          <div className="form-group">
            <label>
              Confirm Transaction Code:{" "}
              <small className="text-danger">
                {TransactionCode != "" && TransactionCode != ConfirmCode
                  ? "Not Matching"
                  : ""}
              </small>
            </label>
            <input
              className="rounded form-control"
              placeholder="Confirm Transaction Code"
              value={ConfirmCode}
              onChange={(e) => setConfirmCode(e.target.value)}
              required
            />
          </div>
        </div>
        <div className="card-footer">
          <div className="d-flex justify-content-around">
            {Loading ? (
              <span className="spinner-border text-primary"></span>
            ) : (
              <button
                type={"submit"}
                className="btn btn-outline-primary rounded"
              >
                Submit
              </button>
            )}
          </div>
        </div>
      </form>
    </div>
  );
};

export default ManualPay;
