import React, { useState } from "react";
import { Button, Form } from "react-bootstrap";
import { ApiUrl } from "../../Network/Urls";
import swal from "sweetalert";
import SubPay from "./SubPay";
import Header from "../../components/Header";

const SoftwarePay = () => {
  const [Loading, setLoading] = useState(false);
  const [SoftwareId, setSoftwareId] = useState("");
  const [ConfirmId, setConfirmId] = useState("");
  const [Pay, setPay] = useState(false);
  const [Error, setError] = useState(false);

  const [Subs, setSubs] = useState([]);

  const HandleSubmit = async (e) => {
    e.preventDefault();
    if (SoftwareId !== ConfirmId) {
      setError(true);
      return;
    }
    setLoading(true);
    await GetSubs();
    setPay(true);
    setLoading(false);
  };
  const GetSubs = async () => {
    await fetch(`${ApiUrl}/sub/fetch`, {
      method: "POST",
      headers: {
        "content-type": "application/json",
        authorization: `bearer `,
      },
      body: JSON.stringify({
        softwareId: SoftwareId.split("").reverse().join(""),
      }),
    })
      .then((res) => res.json())
      .then((res) => {
        if (res.status === 200) {
          setSubs(res.subs);
        } else {
          swal({
            title: "Failed to connect",
            icon: "warning",
            timer: 3000,
          });
        }
      })
      .catch(() => {
        swal({
          title: "Failed to connect",
          icon: "warning",
          timer: 3000,
        });
      });
  };

  return (
    <div>
      <Header></Header>
      <div
        className="row d-flex justify-content-center"
        style={{ marginTop: "40px" }}
      >
        <div className="col-md-6">
          <div className="card">
            <div className="card-header">
              <h6 className="text-center">
                Activate School Techsystem Software
              </h6>
            </div>
            <div className="card-body">
              {Pay ? (
                <SubPay
                  subs={Subs}
                  updatePay={setPay}
                  softwareId={SoftwareId.split("").reverse().join("")}
                ></SubPay>
              ) : (
                <Form onSubmit={HandleSubmit}>
                  <div className="form-group">
                    <label className="mb-2 text-capitalize">Software Id:</label>
                    <input
                      className="rounded form-control"
                      placeholder={`enter software id`}
                      value={SoftwareId}
                      onChange={(e) => setSoftwareId(e.target.value)}
                      required
                    />
                    <hr />
                  </div>
                  <div className="form-group">
                    <label className="mb-2 text-capitalize">
                      Confirm Software Id:
                      {Error ? (
                        <small className="text-danger">
                          <em>Not Matching</em>
                        </small>
                      ) : null}
                    </label>
                    <input
                      className="rounded form-control"
                      placeholder={`confirm software id`}
                      value={ConfirmId}
                      onChange={(e) => setConfirmId(e.target.value)}
                      required
                    />
                  </div>
                  <hr />
                  <div className="d-flex justify-content-around mb-2">
                    {Loading ? (
                      <span className="spinner-border text-primary"></span>
                    ) : (
                      <Button variant="primary" type="submit">
                        Next
                      </Button>
                    )}
                  </div>
                </Form>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SoftwarePay;
