import React from "react";
import Purchase from "./Purchase";
import { ArrowBarLeft } from "react-bootstrap-icons";

const SubPay = ({ subs, updatePay, softwareId }) => {
  return (
    <div>
      {" "}
      <div className=" row d-flex justify-content-center">
        <div className="col-md-6 ">
          <h6 className="text-center">
            {subs.length > 0
              ? "Get Maintenance And Update Code At 29$ only"
              : "Get Activation Code at 99$ only"}
          </h6>
          <p className="text-center">
            <small>
              <em>
                {subs.length > 0
                  ? `Previous Activation Code if not already used ${
                      subs[subs.length - 1].code
                    }`
                  : null}
              </em>
            </small>
          </p>
        </div>
      </div>
      <div className="row d-flex justify-content-center">
        <div className="col-md-6 justify-content-center">
          {subs.length > 0 ? (
            <Purchase
              softwareId={softwareId}
              amount={"29.0"}
              from={"software"}
            ></Purchase>
          ) : (
            <Purchase
              softwareId={softwareId}
              amount={"99.0"}
              from={"software"}
            ></Purchase>
          )}
        </div>
      </div>
      <div className=" row d-flex justify-content-center">
        <div className="col-md-6 d-flex justify-content-around">
          <button
            className="btn btn-sm btn-outline-secondary"
            onClick={() => updatePay(false)}
          >
            <ArrowBarLeft></ArrowBarLeft> Go Back
          </button>
        </div>
      </div>
    </div>
  );
};

export default SubPay;
